@use "global" as *;
@import "./module/mixins";

.l-price {
  @include l-subpage;
}

.price__inner {
  position: relative;
  z-index: 2;
}

.price__inner::before {
  @include subpage-decoration;
}

.price__contents {
  width: 100%;
  @include mq("md") {
    width: rem(700);
    margin-inline: auto;
  }
}
