@use 'global' as *;

.campaign-cards--sidebar .campaign-card:not(:first-child){
  margin-top: rem(40);
}

.campaign-cards{
  width: 80%;
  margin-inline:auto;
  @include mq('md') {
    width: 100%;
    margin-top: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: rem(80) rem(40);
  }
}
