@use "global" as *;

.qa-icon {
  position: absolute;
  top:50%;
  right: rem(16);
  transform: translate(0,-50%);
  width: rem(28);
  height: rem(28);
  @include mq("md") {
    right: rem(40);
    width: rem(32);
    height: rem(32);
  }
}

.qa-icon__vertical {
  display: inline-block;
  position: absolute;
  top:50%;
  width: 100%;
  height: 1px;
  background-color: $white;
  transition: 0.5s;
  transform: rotate(90deg);
}

.qa-box__question.is-open .qa-icon__vertical{
  transform: rotate(0deg);
  transition: 0.5s;
}

.qa-icon__horizontal {
  position: absolute;
  top:50%;
  display: inline-block;
  width: 100%;
  height: 1px;
  background-color: $white;
}
